<template>
  <v-container>
    <v-card id="drop-area" flat>
      <v-card-title class="text-center"> 修改服务器时间 </v-card-title>
      <v-divider></v-divider>
      <v-card-text>
        <v-row>
          <v-col>
            <v-text-field
              label="服务器时差"
              v-model="offset"
              variant="outlined"
            ></v-text-field>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-row justify="center">
          <v-col cols="5">
            <v-btn
              variant="flat"
              color="secondary"
              :loading="uploading"
              @click="modifyopentime"
              block
              >修改</v-btn
            >
          </v-col>
        </v-row>
      </v-card-actions>
    </v-card>
    <v-snackbar v-model="snackbar" :timeout="2000">
      {{ errstr }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import { uploadformData } from "@/utils/upload";
export default {
  name: "upload",
  data() {
    return {
      errstr: "",
      snackbar: false,
      offset: 0,
      uploading: false,
    };
  },
  mounted: function () {},
  methods: {
    modifyopentime() {
      this.uploading = true;
      let formData = new FormData();
      formData.append("offset", this.offset);
      uploadformData("/modifyopentime", formData, null)
        .then((response) => {
          const { data } = response;
          console.log("modifyopentime succ");
          this.uploading = false;
          this.$forceUpdate();
        })
        .catch((err) => {
          this.uploading = false;
          this.errstr = err.message;
          this.snackbar = true;
          console.log("modifyopentime err:%o", err);
        });
    },
  },
};
</script>

<style></style>
